<template>
	<div class="finance-products-risk-assess">
		<div class="field-warn bgWhite">
			<br>
			<br>
1.	<br>
2.	<br>
3.	<br>


		</div>
		<div class="risk-assess">
			<mt-cell title=""></mt-cell>
			<div v-for="(item, index) in datas" class="risk-assess-item">
				<mt-radio
					v-model="answers[index]"
			  		:title="(index + 1) + '. ' + item.question"
			  		:options="item.answers">
				</mt-radio>
			</div>
		</div>
		<cell-password :options=" cellPassword " ></cell-password>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
			<mt-button @click.native="submit" type="primary"></mt-button>
		</div>
		<div class="field-warn">
			
		</div>
	</div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';

import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			answers: [],
			datas: [],
			cellPassword: {
                title: '',
                rule: 'number'
            }
		};
	},
	methods: {
		submit() {
			var _error = [];
			for(let i = 0; i < this.answers.length; i++){
				if(!this.answers[i]){
					_error.push('');
					break;
				}
			}
            if( _error.length > 0 ) {
                Toast({
                    message: _error[0],
                    iconClass: 'error',
                    duration: 1000
                });
                return;
            }
            service.getUserAssess({
            }).then(data => {
				this.$router.push('/finance-products/assess-result');
            });
		},
		back() {
			history.back();
		},
		getAssessQuestion() {
        	service.getAssessQuestion()
        	.then(data => {
        		this.datas = data.data.data;
        		this.answers = new Array(this.datas.length);
        	});
        }
	},
	created() {
		this.getAssessQuestion();
	}
};

</script>

<style lang="scss">

.finance-products-risk-assess{
	background-color: #fff;
	.risk-assess-item{
		padding: 16px;
	}
}

</style>


