<template>
	<div class="transaction-order-back-detail">
		<mt-cell title=""></mt-cell>
		<mt-cell title="" :value="detail.account"></mt-cell>
		<mt-cell title="" :value="detail.code"></mt-cell>
		<mt-cell title="" :value="detail.name"></mt-cell>
		<mt-cell title="" :value="detail.type"></mt-cell>
		<mt-cell title="" :value="detail.money"></mt-cell>
		<mt-cell title="" :value="detail.part"></mt-cell>
		<mt-cell title="" :value="detail.lastDay"></mt-cell>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
			<submit-button @submit="submit" :submitStatus="submitStatus" value=""></submit-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				account: '',
				code: '',
				name: '',
				type: '',
				money: '',
				part: '',
				lastDay: ''
			},
			submitStatus: false,
			cellPassword: {
                title: '',
                rule: 'number'
            }
		};
	},
	methods: {
		submit() {
			service.orderBackSubmit()
			.then(data => {
				this.$router.push(`/finance-products/transaction/order-back/success/${this.$route.params.id}`);
			});
		},
		back() {
			history.back();
		},
		getOrderBackDetail(id) {
			service.getOrderBackDetail({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getOrderBackDetail(this.$route.params.id);
	}
};

</script>


