<template>
	<div class="finance-products-risk-search">
		<mt-cell title=""></mt-cell>
		<mt-cell title="" v-model="detail.grade" ></mt-cell>
		<mt-cell title="" v-model="detail.endDate" ></mt-cell>
		<mt-cell class="colorRed" title="" v-model="detail.score" ></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
			<mt-button @click.native="submit" type="primary"></mt-button>
		</div>
		<div class="field-warn">
			<br>

		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			detail: {
				grade: '',
				endDate: '',
				score: ''
			}
		};
	},
	methods: {
        submit() {
        	this.$router.push('/finance-products/risk-assess');
        },
        back() {
        	history.back();
        },
        getUserRisk(id) {
        	service.getUserRisk({
        		id: id
        	})
        	.then(data => {
        		this.detail = data.data.data;
        	});
        }
	},
	created() {
		this.getUserRisk(this.$route.params.id);
	}
};

</script>

<style lang="scss">
	
.finance-products-risk-search{
	.mint-cell-title{
		width: 5rem;
	}
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}

</style>


