<template>
	<div class="transaction-search-transaction-detail">
		<mt-cell title=""></mt-cell>
		<mt-cell title="" :value="detail.account"></mt-cell>
		<mt-cell title="" :value="detail.number"></mt-cell>
		<mt-cell title="" :value="detail.custom"></mt-cell>
		<mt-cell title="" :value="detail.customType"></mt-cell>
		<mt-cell title="" :value="detail.idCardType"></mt-cell>
		<mt-cell title="" :value="detail.idCard"></mt-cell>
		<mt-cell title="" :value="detail.transType"></mt-cell>
		<mt-cell title="" :value="detail.code"></mt-cell>
		<mt-cell title="" :value="detail.name"></mt-cell>
		<mt-cell title="" :value="detail.applyMoney"></mt-cell>
		<mt-cell title="" :value="detail.applyPart"></mt-cell>
		<mt-cell title="" :value="detail.transMessage"></mt-cell>
		<mt-cell title="" :value="detail.transDate"></mt-cell>
		<mt-cell title="" :value="detail.transChannel"></mt-cell>
		<mt-cell title="" :value="detail.transStatus"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				id: '',
				account: '',
				number: '',
				custom: '',
				customType: '',
				idCardType: '',
				idCard: '',
				transType: '',
				code: '',
				name: '',
				applyMoney: '',
				applyPart: '',
				transMessage: '',
				transDate: '',
				transChannel: '',
				transStatus: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push('/finance-products/search/transaction');
		},
		getSearchTransactionDetail(id) {
			service.getSearchTransactionDetail({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getSearchTransactionDetail(this.$route.params.id);
	}
};

</script>


