<template>
	<div class="transaction-order-back">
		<mt-cell title=""></mt-cell>
        <mt-cell @click.native="showSelectorPaymentAccount()" title="" placeholder="" v-model="account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
        </mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
            <submit-button @submit="submit" :submitStatus="submitStatus" value=""></submit-button>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			account: '',
            submitStatus: false,
			selectorPaymentAccount: {
                title: '',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '',
                        account: '625228***0089',
                        type: '',
                        isDefault: true,
                        idCardType: '',
                        idCard: '550505****50505'
                    },
                    {
                        id: 2,
                        name: '2',
                        account: '625228***0025',
                        type: ''
                    },
                    {
                        id: 3,
                        name: '3',
                        alias: '',
                        account: '625228***0109',
                        type: '',
                        bankName: ''
                    }
                ]
            }
		};
	},
    watch: {
        account: function(val) {
            if(val){
                this.submitStatus = true;
            }else{
                this.submitStatus = false;
            }
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.account = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.checkProductHas(this.account);
        },
        back() {
        	history.back();
        },
        checkProductHas(account) {
            service.getOrderBackForAccount({
                account: account
            }).then(data => {
                if(!data.data.data || data.data.data.length === 0){
                    var self = this;
                    MessageBox({
                        title: '',
                        message: '',
                        confirmButtonText: '',
                        closeOnClickModal: false
                    }).then(action => {
                    });
                }else{
                    this.$router.push(`/finance-products/transaction/order-back/list/${this.account}`);
                }
            });
        }
	},
	created() {
		this.account = this.account || this.$store.state.orderBack.account;
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateOrderBack', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateOrderBack', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.transaction-order-back{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


