<template>
	<div class="transaction-product-apply-agree">
		<mt-cell title=""></mt-cell>
		<mt-cell title="" is-link></mt-cell>
		<mt-cell title="" is-link></mt-cell>
		<mt-cell title="" is-link></mt-cell>
		<mt-cell title="" is-link></mt-cell>
		<div class="field-warn">
			
			<ol>
				<li>1. </li>
				<li>2. </li>
			</ol>
		</div>
		<mt-cell class="text-center" title=""></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
			<mt-button @click.native="agree" type="primary"></mt-button>
		</div>
		<div class="field-warn">
			<ol>
				<li>1. </li>
				<li>2. </li>
			</ol>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
export default {
	data() {
		return {
		};
	},
	methods: {
		back() {
			history.back();
		},
		agree() {
			var self = this;
			MessageBox({
			 	title: '',
			  	message: '',
			  	showCancelButton: true
			}).then(action => {
				if(action === 'confirm'){
					this.$router.push('/finance-products/transaction/product-apply/detail/' + this.$route.params.id);
				}
			});
		}
	}
};

</script>

<style lang="scss">

.transaction-product-apply-agree{
	.field-warn{
		background-color: #fff;
	}
}

</style>


