<template>
	<div class="transaction-product-order-buy">
		<mt-cell title=""></mt-cell>
		<mt-cell title="" :value="detail.account"></mt-cell>
		<mt-cell title="" :value="detail.idCard"></mt-cell>
		<mt-cell title="" :value="detail.money"></mt-cell>
		<mt-cell title="" :value="detail.startMoney"></mt-cell>
		<mt-cell title="" :value="detail.addMoney"></mt-cell>
		<mt-cell title="" :value="detail.firstMoney"></mt-cell>
		<mt-field type="number" v-model="buyMoney" label=""></mt-field>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
			<mt-button @click.native="submit" type="primary"></mt-button>
		</div>
	</div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';
import service from '@/services/bank/finance-products';

export default {
	data() {
		return {
			detail: {
				account: '',
				idCard: '',
				money: '',
				startMoney: '',
				addMoney: '',
				firstMoney: ''
			},
			buyMoney: '',
			cellPassword: {
                title: '',
                rule: 'number'
            }
		};
	},
	methods: {
		submit() {
			if(!this.buyMoney){
				Toast({
                    message: '',
                    iconClass: 'error',
                    duration: 1000
                });
                return;
			}
			service.getProductBuy()
			.then(data => {
				this.$router.push(`/finance-products/transaction/product-order/success/${this.$route.params.id}`);
			});
		},
		back() {
			history.back();
		},
		getProductBuyDetail(id) {
			service.getProductBuyDetail({
				id: id
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getProductBuyDetail(this.$route.params.id);
	}
};

</script>

<style lang="scss">

.transaction-product-order-buy{
	.mint-cell-title{
		width: 5rem;
	}
}

</style>


